"use strict";

import Namespace from './Namespace';
import JSObjectNamespace from './JSObjectNamespace';

export default class ListNamespace extends Namespace {

    constructor(list) {
        super();

        this.list = list;
        this.commonType = ListNamespace.getCommonType(list);
        this.originalSize = list.length;
        this.originalType = typeof list;
    }

    callMethod(name, args) {
        switch (name) {
            case "type":
                return new JSObjectNamespace(this.originalType);
            case "size":
                return new JSObjectNamespace(this.list.length);
            case "original_size":
                return new JSObjectNamespace(this.originalSize);
            default:
                return super.callMethod(name, args);
        }
    }

    static getCommonType(list) {
        return "array";
    }

    readKey(key) {
        return this.list[key];
    }
}
